body {
  background-color: rgb(44, 62, 80);
  color: rgb(255, 255, 255);
  font-family: 'Open Sans', sans-serif;
  text-align: center;
}

#root {
  position: relative;
  min-height: 100vh;
}

#content-wrap {
  padding-bottom: 6rem;
}

h1 {
  font-size: 3em;
}

h2 {
  font-family: 'Open Sans', sans-serif;
  font-weight: 100;
  font-size: 2em;
}

ul.social-media-list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 30%;
  left: 50%;
  position: relative;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin-top: 40px;
  padding: 0px;
}

ul.social-media-list li {
  list-style: none;
  padding: 0px;
}

  ul.social-media-list li a{
    text-decoration: none;
    color: #fff;
  }

    ul.social-media-list li a:hover{
      text-shadow: 0px 0px 15px #fff;
    }

@media only screen and (max-width: 768px) {
div.container{
  max-width: calc(100% - 60px);
  top: calc(50% - 50px);
}

h1 {
  font-size: 2em;
}

h2 {
  font-family: 'Open Sans', sans-serif;
  font-weight: 100;
  font-size: 1em;
}

ul.social-media-list {
  margin-top: 10%;
  width: 80%;
}
}
.bio {
  padding-top: 20px;
}

.avatar {
  margin-top: 20px;
  border-radius: 100%;
  height: 200px;
  width: 200px;
}

@media only screen and (max-width: 768px) {
  height: 150px;
  width: 150px;
}
footer {
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  position: absolute;
  bottom: 0px;
  width: 100%;
  text-align: center;
  overflow: hidden;
}

footer div.border {
  width: 60px;
  position: absolute;
  top: 0px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  border-top: 1px solid;
}

footer address {
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: 100;
  position: relative;
  margin-bottom: 10px;
  margin-top: 5px;
  text-decoration: none;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  padding: 0px 10px;
  width: 50%;
  
}
form {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 20px;
}

.form-container {
  width: 100%;
}

.btn-action {
  margin-left: 20px;
  margin-right: 20px;
}

.form-group{
  width: 60%;
}

#contact-button{
  margin-top: 30px;
}

@media only screen and (max-width: 768px) {
  .form-group{
    width: 90%;
  }
}


